<template>
  <n-config-provider :theme="theme" :theme-overrides="themeOverrides">
    <n-global-style />
    <n-layout>
      <n-layout-header bordered class="toolbar">
        <n-space justify="end" align="center">
          <span>{{ dark ? 'Dark Mode' : 'Light Mode' }}</span>
          <!-- Áõ¥Êé•Áî® v-model ÁªëÂà∞ ref(boolean)ÔºåÂèØÂàáÊç¢ÂõûÂéª -->
          <n-switch v-model:value="dark">
            <template #checked>Dark</template>
            <template #unchecked>Light</template>
          </n-switch>
        </n-space>
      </n-layout-header>

      <n-layout-content content-style="padding: 24px;">
        <router-view />
      </n-layout-content>
    </n-layout>
  </n-config-provider>
</template>

<script setup>
import { ref, computed, onMounted, watchEffect } from 'vue'
import { darkTheme } from 'naive-ui'

// Áî® boolean ÊéßÂà∂‰∏ªÈ¢òÔºåÈÅøÂÖçÁõ¥Êé•ÊØîËæÉÂØπË±°ÂºïÁî®
const dark = ref(false)

// Áî± boolean Ê¥æÁîüÂá∫ Naive ÁöÑ‰∏ªÈ¢òÂØπË±°Ôºànull = ÊµÖËâ≤Ôºâ
const theme = computed(() => (dark.value ? darkTheme : null))

// ÂèØÈÄâÔºöÊåÅ‰πÖÂåñÔºåÂà∑Êñ∞Âêé‰øùÊåÅÁî®Êà∑ÈÄâÊã©
onMounted(() => {
  const saved = localStorage.getItem('demo-dark')
  if (saved != null) dark.value = saved === '1'
})
watchEffect(() => {
  localStorage.setItem('demo-dark', dark.value ? '1' : '0')
})

// ‰Ω†Ëá™Â∑±ÁöÑË¶ÜÁõñËâ≤
const themeOverrides = {
  common: {
    primaryColor: '#1E88E5',
    primaryColorHover: '#42A5F5',
    primaryColorPressed: '#1565C0'
  }
}
</script>

<style>
.toolbar { padding: 8px 16px; }
</style>